
.startButton {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
    background-image: url(./img/microphone.png);
    background-color: red;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 100px;
    width: 100px;
    border: none;
    border-radius: 50%;
	background-size: cover;
}

.startButton span {
	display: none;
}

.audioControls {
	position: absolute;
	top: 80%;
	left: 50%;
	transform: translate(-50%, -50%);
}
